<template>
  <div class="flex gap-5 items-center">
    <button
      v-if="hasNextPrevButton"
      class="w-20 py-2 rounded-md bg-gray-100"
      @click="prevPage"
    >
      上一{{ unit }}
    </button>
    <v-select
      :value="currentPage"
      @input="changePage"
      :options="options"
      :searchable="false"
      :clearable="false"
      :reduce="(option) => option.value"
      :calculate-position="withPopper"
      append-to-body
      class="w-32 flex-shrink-0 cus-select bg-white py-2"
    />
    <button
      v-if="hasNextPrevButton"
      class="w-20 py-2 rounded-md bg-gray-100"
      @click="nextPage"
    >
      下一{{ unit }}
    </button>
  </div>
</template>
<script>
import { createPopper } from "@popperjs/core";

export default {
  props: {
    hasNextPrevButton: {
      default: true,
      type: Boolean,
    },
    unit: {
      default: "",
      type: String,
    },
    totalPage: {
      default: 1,
      type: Number,
    },
    currentPage: {
      default: 1,
      type: Number,
    },
  },
  computed: {
    options() {
      const array = [];
      for (let i = 1; i <= this.totalPage; i++) {
        array.push({
          label: `${i}/${this.totalPage}${this.unit}`,
          value: i,
        });
      }
      return array;
    },
  },
  methods: {
    withPopper(dropdownList, component, { width }) {
      dropdownList.style.width = width;
      const popper = createPopper(component.$refs.toggle, dropdownList, {
        placement: "bottom",
        modifiers: [
          {
            name: "offset",
            options: {
              offset: [0, -1],
            },
          },
          {
            name: "toggleClass",
            enabled: true,
            phase: "write",
            fn({ state }) {
              component.$el.classList.toggle(
                "drop-up",
                state.placement === "top"
              );
            },
          },
        ],
      });

      return () => popper.destroy();
    },
    changePage(page) {
      this.$emit("onChangePage", page);
    },
    nextPage() {
      if (this.currentPage === this.totalPage) return;
      this.changePage(this.currentPage + 1);
    },
    prevPage() {
      if (this.currentPage === 1) return;
      this.changePage(this.currentPage - 1);
    },
  },
};
</script>
